@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
  }
  
  body {
    font-family: 'Inter', system-ui, sans-serif;
    line-height: 1.6;
  }

  /* Improve text rendering */
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  /* Better focus indicators for accessibility */
  *:focus {
    outline: 2px solid #2dabe1;
    outline-offset: 2px;
  }

  /* Smooth scrolling for anchor links */
  html {
    scroll-padding-top: 120px;
  }
}

@layer components {
  .btn-primary {
    @apply bg-secondary-500 hover:bg-primary-400 text-white font-semibold py-3 px-6 rounded-lg transition-all duration-300 transform hover:scale-105 shadow-lg hover:shadow-xl;
  }
  
  .btn-secondary {
    @apply bg-secondary-500 hover:bg-secondary-600 text-white font-semibold py-3 px-6 rounded-lg transition-all duration-300 transform hover:scale-105 shadow-lg hover:shadow-xl;
  }
  
  .btn-outline {
    @apply border-2 border-primary-500 text-primary-500 hover:bg-primary-400 hover:text-white hover:border-primary-400 font-semibold py-3 px-6 rounded-lg transition-all duration-300 transform hover:scale-105;
  }
  
  .btn-outline-secondary {
    @apply border-2 border-secondary-500 text-secondary-500 hover:bg-secondary-500 hover:text-white hover:border-secondary-500 font-semibold py-3 px-6 rounded-lg transition-all duration-300 transform hover:scale-105;
  }
  
  .section-padding {
    @apply py-16 px-4 sm:px-6 lg:px-8;
  }
  
  .container-custom {
    @apply max-w-7xl mx-auto px-4 sm:px-6 lg:px-8;
  }
  
  .card {
    @apply bg-white rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 transform hover:-translate-y-2;
  }
  
  .input-field {
    @apply w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all duration-300;
  }
  
  .gradient-bg {
    @apply bg-gradient-to-br from-primary-50 via-white to-secondary-50;
  }

  /* Responsive text utilities */
  .text-responsive-xl {
    @apply text-2xl sm:text-3xl lg:text-4xl xl:text-5xl;
  }

  .text-responsive-lg {
    @apply text-xl sm:text-2xl lg:text-3xl;
  }

  .text-responsive-md {
    @apply text-lg sm:text-xl lg:text-2xl;
  }

  /* Responsive spacing utilities */
  .section-responsive {
    @apply py-8 sm:py-12 lg:py-16 xl:py-20;
  }

  .container-responsive {
    @apply max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 xl:px-12;
  }

  /* Mobile-first grid utilities */
  .grid-responsive {
    @apply grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4;
  }

  .grid-responsive-2 {
    @apply grid grid-cols-1 lg:grid-cols-2;
  }

  .grid-responsive-3 {
    @apply grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3;
  }

  /* Responsive navigation */
  .nav-responsive {
    @apply hidden lg:flex;
  }

  .nav-mobile {
    @apply lg:hidden;
  }

  /* Responsive images */
  .img-responsive {
    @apply w-full h-auto object-cover;
  }

  /* Responsive forms */
  .form-responsive {
    @apply space-y-4 sm:space-y-6;
  }

  .input-responsive {
    @apply w-full px-3 py-2 sm:px-4 sm:py-3 text-sm sm:text-base;
  }

  /* Responsive buttons */
  .btn-responsive {
    @apply w-full sm:w-auto px-4 py-2 sm:px-6 sm:py-3 text-sm sm:text-base;
  }

  /* Responsive cards */
  .card-responsive {
    @apply p-4 sm:p-6 lg:p-8;
  }

  /* Responsive hero section */
  .hero-responsive {
    @apply min-h-[60vh] sm:min-h-[70vh] lg:min-h-[80vh] xl:min-h-screen;
  }

  /* Responsive typography */
  .heading-responsive {
    @apply text-2xl sm:text-3xl lg:text-4xl xl:text-5xl font-bold;
  }

  .subheading-responsive {
    @apply text-lg sm:text-xl lg:text-2xl;
  }

  .body-responsive {
    @apply text-sm sm:text-base lg:text-lg;
  }
}

@layer utilities {
  .text-gradient {
    @apply bg-gradient-to-r from-primary-500 to-secondary-500 bg-clip-text text-transparent;
  }
  
  .shadow-custom {
    box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  }

  /* Responsive utilities */
  .hidden-mobile {
    @apply hidden sm:block;
  }

  .hidden-desktop {
    @apply block sm:hidden;
  }

  .text-center-mobile {
    @apply text-center sm:text-left;
  }

  .text-left-mobile {
    @apply text-left sm:text-center;
  }

  /* Accessibility utilities */
  .sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
  }

  .focus-visible {
    @apply focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2;
  }

  /* Performance utilities */
  .will-change-transform {
    will-change: transform;
  }

  .will-change-opacity {
    will-change: opacity;
  }

  /* Print styles */
  @media print {
    .no-print {
      display: none !important;
    }
    
    .print-only {
      display: block !important;
    }
  }
}

/* Newspaper Styles */
.newspaper-layout {
  font-family: 'Georgia', 'Times New Roman', serif;
}

.newspaper-header {
  border-bottom: 4px solid #000;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
}

.newspaper-title {
  font-family: 'Times New Roman', serif;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  letter-spacing: 2px;
}

.newspaper-subtitle {
  font-family: 'Georgia', serif;
  font-style: italic;
}

.newspaper-date {
  font-family: 'Courier New', monospace;
  letter-spacing: 1px;
}

.main-headline {
  border-left: 8px solid #2dabe1;
  background: linear-gradient(135deg, #ffffff 0%, #f8f9fa 100%);
}

.newspaper-content {
  background: #ffffff;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.newspaper-article {
  text-align: justify;
}

.newspaper-article h2 {
  font-family: 'Times New Roman', serif;
  color: #1f2937;
  border-bottom: 2px solid #e5e7eb;
  padding-bottom: 0.5rem;
}

.newspaper-article p {
  font-family: 'Georgia', serif;
  line-height: 1.8;
  text-align: justify;
  hyphens: auto;
}

.newspaper-services {
  border-top: 4px solid #000;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
}

/* Line clamp utilities */
.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

/* Newspaper Carousel Styles */
.newspaper-container {
  perspective: 1000px;
}

.newspaper-page {
  background: #ffffff;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  transition: all 0.3s ease;
}

.newspaper-page:hover {
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
  transform: translateY(-2px);
}

.page-header {
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
  position: relative;
}

.page-header::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #2dabe1, #ec1c26);
}

/* Responsive newspaper styles */
@media (max-width: 768px) {
  .newspaper-article {
    text-align: left;
  }
  
  .newspaper-article p {
    text-align: left;
  }
  
  .newspaper-title {
    font-size: 1.5rem;
    letter-spacing: 1px;
  }
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #2dabe1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #ec1c26;
}

/* Loading animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.animate-fade-in {
  animation: fadeIn 0.6s ease-out;
}

.animate-slide-in {
  animation: slideIn 0.5s ease-out;
}

.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Responsive animations */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .btn-primary,
  .btn-secondary {
    border: 2px solid currentColor;
  }
  
  .card {
    border: 1px solid #000;
  }
}

/* Dark mode support (if needed in future) */
@media (prefers-color-scheme: dark) {
  /* Dark mode styles can be added here */
}

/* Landscape orientation adjustments */
@media (orientation: landscape) and (max-height: 500px) {
  .hero-responsive {
    min-height: 100vh;
  }
  
  .section-responsive {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
}

/* Large screen optimizations */
@media (min-width: 1536px) {
  .container-custom {
    max-width: 1400px;
  }
  
  .text-responsive-xl {
    font-size: 3.5rem;
  }
}

/* Touch device optimizations */
@media (hover: none) and (pointer: coarse) {
  .btn-primary,
  .btn-secondary,
  .btn-outline {
    min-height: 44px;
    min-width: 44px;
  }
  
  .input-field {
    min-height: 44px;
  }
  
  /* Increase touch targets */
  button,
  a {
    min-height: 44px;
    min-width: 44px;
  }
}
